
html{
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#403c3c+0,191818+100 */
	background: #403c3c; /* Old browsers */
	background: -moz-linear-gradient(-45deg, #403c3c 0%, #191818 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(-45deg, #403c3c 0%,#191818 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(135deg, #403c3c 0%,#191818 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#403c3c', endColorstr='#191818',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
	background-attachment: fixed;
}

body{
	background: transparent;
	color: $primary-color;
	font-size: 14px;
	font-family: $text-serif;
}

.text-sans{	
	font-family: $text-sans;	
	letter-spacing: 0.1em;
}



.content-area{
	width: 100%;
}

a{
	color: #439eff;
}
h1, h2, h3,
h1.widget-title, h3.sidetitle
{
	color: $primary-color;
	border: none;
	font-family: $text-sans
}


h1{
	border: none;
	&.entry-title{
		border: none;
	}
}
h3.subtitle{
	font-size: 1em;
	font-family: $text-sans;
	font-weight: 600;
	letter-spacing: 0.2em;
	opacity: 0.4;
}

hr{
	border-color: $primary-color;
	opacity: 0.3;
}

a, a:hover, a:visited, a:focus{
	text-decoration: none;
}

#main, .widget{
	padding: 0px;
	border: 0;
	border-radius: 0;
	&.post-list{
		border: none !important;
		padding: 0 !important;
	}
}

.iframe-wrapper{
	position: relative;
	width: 100%;
	height: 0;
	margin: 30px 0;
	padding-top: 62.5%;
	iframe {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
	}
	&.iframe-1366{
		padding-top: 56.223%;
	}

	&.iframe-1280, &.iframe-1440{
		padding-top: 62.5%;
	}
}


.image-bg{
	background-repeat: no-repeat;
	background-position: center center;
	// background-attachment: fixed;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}


.vc-wrapper {
	display: table;
	height: 100%;
	&.full-height {
		height: 100%;
	}
	&.full-width {
		width: 100%;
	}
}
.vc-inside{
	display: table-cell;
	vertical-align: middle;
}


.site-content{
	padding: 0!important;
}

button.btn, .btn:not(:disabled):not(.disabled){
	outline: none !important;
	&.btn-default{
		@include gradient-bg-1;
		color: white;
		&:hover, &:active, &:focus, &:visited{
			color: white;
			@include transition(all 0.3s ease-in-out);
			@include gradient-bg-1;
			opacity: 0.7;
		}
	}
}
button, a.button{
	color: white;
	@include primary-gradient;
	font-size:0.8em;
	border-radius: 100px;
	padding:12px 35px;	
	font-weight: bold;
	text-transform: uppercase;
	border: none;
	outline: none;
	text-decoration: none !important;
	@include transition(all 0.3s ease-in-out);
	&:hover{
		opacity: 0.9;
	}

	&.button-primary{
		color: white;
		@include primary-gradient;
	}
	&.button-secondary{
		color: white;
		background: $secondary-color;
		&:hover{		
			background: lighten($secondary-color, 25%);
		}
	}

	&.button-ghost{
		background: transparent;
		color: lighten($primary-color, 85%);		
		border: 2px solid lighten($primary-color, 65%);
		padding: 10px 35px;	
		&:hover{		
			background: rgba(255,255,255,0.3);
		}
	}

	&.button-default{
		color: $primary-color;
		background: #E2E3E5;
		&:hover{		
			background: #F2F5F6;
		}
	}

	&.button-white{
		color: $primary-color;
		background: white;	
		&:hover{		
			background: lighten($primary-color, 55%);
		}
	}

}



.multiple-blocks {
	.block-list{
		.block{
			.title{
				font-size: 1.2em;
				font-weight: 600;
				line-height: 1.2;

			}
			.content, .content p{
				margin-top: 20px;
				line-height: 165%;
				opacity: .75;
			}
			p{
				margin: 0;
			}
			.image{
				img{
					max-width: 200px;
				}
			}
		}
		a{
			@include transition(all 0.3s ease-in-out);
			&:hover{
				opacity: 0.8;
			}
		}
	}
	&.white-content{
		.title, .subtitle, .content, .content p{
			color: white;
		}
		.block-list .title{
			opacity: 0.6;
		}
	}
}
.faq-list{

	.question-wrapper{
		box-shadow: 2px 2px 6px rgba(0,0,0,0.15);
		margin: 0 0 10px 0;
		a.question{
			display: block;
			padding: 20px 60px 20px 20px;
			color: $primary-color;
			border-radius: 2px;
			position: relative;			
			background: white;
			.icon{
				position: absolute;
				right: 0;
				top: 0;
				padding: 20px;
				.up, .down{
					color: $primary-color;
					font-size: 1em;
				}
				.down{
					display: none;
				}
			}	
			&.collapsed{
				background: #FBFBFB;
				.down{
					display: block;
				}
				.up{
					display: none;
				}
			}

		}
		.collapse{
			
		}
		.answer{
			padding: 20px;
			background: white;
			opacity: 0.6;
		}
	}
}


